
import { Vue, Component, Prop } from "vue-property-decorator";
import NiceButton from "@/components/UI/NiceButton.vue";
import Icon from "@/components/UI/Icon.vue";
import { DesignProvider } from "@/types/design-library";
import { logEvent } from "@/util/tracking";

@Component({
  components: { Icon, NiceButton }
})
export default class DesignLibraryItem extends Vue {
  @Prop()
  provider?: DesignProvider;

  previewProvider() {
    logEvent("Design Library", {
      action: "provider_previewed",
      provider: this.provider?.name
    });

    window.open(this.provider?.url, "_blank");
  }
}
