
import "@/sass/globals.scss";

import Vue from "vue";
import { Component } from "vue-property-decorator";
import Loading from "@/components/Reusable/Loading.vue";
import { pipe } from "fp-ts/function";
import { fold } from "fp-ts/Either";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import {
  DesignLibraryData,
  DesignLibraryDataV,
  DesignProvider,
  DesignSetDataV
} from "@/types/design-library";
import NiceButton from "@/components/UI/NiceButton.vue";
import Dialog from "@/components/UI/Dialog.vue";
import Icon from "@/components/UI/Icon.vue";
import DesignLibraryStatusIcon from "@/subapps/design-library/DesignLibraryStatusIcon.vue";
import DesignLibraryImportingDialog from "@/subapps/design-library/DesignLibraryImportingDialog.vue";
import Notifications from "@/components/Notifications/Notifications.vue";
import AlertBox from "@/components/Reusable/CustomInputs/AlertBox.vue";
import { centerDialogInScreen } from "@/subapps/manage-templates/lib/modal-position";
import { setLastUsedDesignProviderUrl } from "@/lib/design-library/design-library";
import DesignLibraryPassword from "@/components/Add/library/DesignLibraryPassword.vue";
import LoadingSpinner from "@/components/Reusable/LoadingSpinner.vue";
import DesignLibraryItem from "@/subapps/design-library/DesignLibraryItem.vue";
import DesignLibraryConfirmImportDialog from "@/subapps/design-library/DesignLibraryConfirmImportDialog.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    DesignLibraryConfirmImportDialog,
    DesignLibraryItem,
    LoadingSpinner,
    DesignLibraryPassword,
    AlertBox,
    Notifications,
    DesignLibraryImportingDialog,
    DesignLibraryStatusIcon,
    Icon,
    Dialog,
    NiceButton,
    Loading
  }
})
export default class ManageDesignLibrary extends Vue {
  isLoading = true;
  loadedData: DesignLibraryData = {
    designProviders: [],
    globalSettingsLastImportedFromUrl: null,
    designPresetsLastImportedFromUrl: null,
    fullSiteLastImportedFromUrl: null
  };
  showOverrideWarning = false;
  showImportingDialog = false;
  providerToImport?: DesignProvider | null = null;
  loadingDesignSetData = false;

  requiresPassword = false;
  passwordValid = true;

  get showPasswordForm() {
    return this.requiresPassword && !this.passwordValid;
  }

  async mounted() {
    pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_get_design_library_data",
        decoder: DesignLibraryDataV
      }),
      fold(
        () => {
          this.isLoading = false;
        },
        data => {
          this.loadedData = data;
          this.isLoading = false;
          logEvent("Design Library", {
            action: "design_library_viewed"
          });
        }
      )
    );
  }

  get designProviders() {
    return this.loadedData.designProviders
      .filter(designProvider => !designProvider.isLocal)
      .filter(designProvider => designProvider.type === "complete_site")
      .reverse();
  }

  async getDesignSetData(maybePassword?: string) {
    let url = this.providerToImport?.url;
    this.loadingDesignSetData = true;

    if (!url) return;

    // If a password is provided, append it to the URL
    if (maybePassword) {
      const searchParams = new URLSearchParams();
      const newUrl = new URL(url);

      searchParams.append("password", maybePassword);
      newUrl.search = searchParams.toString();

      url = newUrl.toString();
    }

    pipe(
      await makeWordPressAjaxRequest({
        action: this.providerToImport?.isLocal
          ? "breakdance_get_local_design_set"
          : "breakdance_get_external_design_set",
        decoder: DesignSetDataV,
        friendlyName: "Design Set Data",
        dataToPost: { url }
      }),
      fold(
        () => {},
        designSetData => {
          this.requiresPassword = designSetData.requiresPassword || false;
          this.passwordValid = !!designSetData.passwordValid;
        }
      )
    );

    this.loadingDesignSetData = false;
  }

  setProviderToImport(siteUrl: DesignProvider["url"]) {
    this.showOverrideWarning = true;
    centerDialogInScreen();
    this.providerToImport = this.designProviders.find(
      provider => provider.url === siteUrl
    );

    this.getDesignSetData();
  }

  closeImportingDialog() {
    this.showImportingDialog = false;
    this.providerToImport = null;
  }

  closeOverrideWarningDialog() {
    this.showOverrideWarning = false;
    this.providerToImport = null;
  }

  async importFullSite() {
    this.showOverrideWarning = false;
    this.showImportingDialog = true;

    if (this.providerToImport) {
      this.loadedData.fullSiteLastImportedFromUrl = this.providerToImport.url;
      setLastUsedDesignProviderUrl(this.providerToImport.url);
    }
  }
}
